/**
 * UUID conversion utilities
 *
 * Converts agent IDs (like "agent-a4a6215") to deterministic UUIDs
 * using UUID v5 (name-based UUID) to ensure the same agent ID always
 * generates the same UUID.
 */

import { v5 as uuidv5 } from "uuid";

/**
 * Namespace UUID for generating deterministic UUIDs from agent IDs.
 * This is a fixed UUID that ensures consistent UUID generation.
 * Using a fixed namespace ensures that the same agent ID always
 * generates the same UUID across different runs.
 */
const AGENT_ID_NAMESPACE = "6ba7b810-9dad-11d1-80b4-00c04fd430c8";

/**
 * Converts an agent ID (e.g., "agent-a4a6215") to a deterministic UUID.
 * The same agent ID will always generate the same UUID.
 *
 * @param agentId - The agent ID string (e.g., "agent-a4a6215")
 * @returns A UUID string
 */
export function agentIdToUuid(agentId: string): string {
  return uuidv5(agentId, AGENT_ID_NAMESPACE);
}

/**
 * Checks if a string is already a valid UUID format.
 *
 * @param str - The string to check
 * @returns True if the string is a valid UUID format
 */
export function isUuid(str: string): boolean {
  const uuidRegex =
    /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i;
  return uuidRegex.test(str);
}

/**
 * Converts an agent ID to UUID if it's not already a UUID.
 * If the input is already a valid UUID, returns it unchanged.
 *
 * @param id - The ID to convert (agent ID or UUID)
 * @returns A UUID string
 */
export function ensureUuid(id: string): string {
  if (isUuid(id)) {
    return id;
  }
  return agentIdToUuid(id);
}
